import React, { useState } from 'react';

export const CreateMatchForm = (props) => {
    const [newMatch, setNewMatch] = useState({ players: ['', ''], firstTo: 11, score: [0, 0], serving: 0, states: [], winner: null });
    const players = props.players;
    const createMatch = props.createMatch;

    function setServingTeam(team) {
        const prevNewMatch = JSON.parse(JSON.stringify(newMatch));
        prevNewMatch.serving = parseInt(team);
        setNewMatch(prevNewMatch);
    }

    function setTeam(e, team) {
        const prevNewMatch = JSON.parse(JSON.stringify(newMatch));
        console.log(e.target.value, team);
        prevNewMatch.players[team] = e.target.value;
        setNewMatch(prevNewMatch);
      }

    return <form onSubmit={e => {e.preventDefault(); createMatch(newMatch)}}>
    {
      [0, 1].map(team => {
        return <div key={`team-${team}-div`}>
          <label htmlFor={`team-${team}-select`}>Team {team + 1}</label><br />
          <select name={`team-${team}-select`} value={newMatch.players[team]} onChange={e => { setTeam(e, team) }}>
            <option value=''></option>
            {
              players.map(player => {
                return <option key={`team-${team}-${player}`} value={player}>{player}</option>
              })
            }
          </select>
        </div>
      })
    }
    <br />
    <p>Service</p>
    <input type="radio" id="team-0-serve" name="serve" value="0" checked={newMatch.serving===0} onChange={e => {setServingTeam(e.target.value)}}/>
    <label htmlFor="team-0-serve">Team 1</label><br/>
    <input type="radio" id="team-1-serve" name="serve" value="1"  checked={newMatch.serving===1} onChange={e => {setServingTeam(e.target.value)}}/>
    <label htmlFor="team-1-serve">Team 2</label><br/>
    <br/>
    <input type='submit' value='Create Match' />
  </form>
}