export const MatchSummary = (props) => {
    const match = props.match;
    console.log(match);
    return <div>
        <button style={{width: '180px', borderRadius: '5px'}} onClick={props.onClick}>
            {match.winner === 0 ? "üèÜ " : ""}
            {match.players[0]} {match.score[0]} - {match.score[1]} {match.players[1]}
            {match.winner === 1 ? " üèÜ" : ""}
        </button>
    </div>
}