import React, { useState } from 'react';

export const CreatePlayerForm = (props) => {
    const [newPlayer, setNewPlayer] = useState('');
    const createPlayer = props.createPlayer;
    const onSubmit = (e) => {
        e.preventDefault();
        createPlayer(newPlayer);
        setNewPlayer('');
    }
    return <form onSubmit={onSubmit}>
        <input style={{width: '120px'}} type='text' onChange={e => setNewPlayer(e.target.value)} value={newPlayer} />
        <br/>
        <input type='submit' value='+ Add' />
    </form>
}