
import React, { useState } from 'react';
import { CreateMatchForm } from './CreateMatchForm';
import { CreatePlayerForm } from './CreatePlayerForm';
import { MatchSummary } from './MatchSummary';

function App() {

  // const [players, SetPlayers] = useState(['Samuel', 'Sarah', 'Eric', 'Michael', 'Rohan']);

  // const [matches, setMatches] = useState([
  //   { players: ['Eric', 'Sarah'], firstTo: 11, score: [5, 11], winner: 1, serving: 0, states: [] },
  //   { players: ['Samuel', 'Michael'], firstTo: 11, score: [14, 12], winner: 0, serving: 1, states: [] }
  // ]);

  const [players, SetPlayers] = useState(['Samuel', 'Sarah', 'Eric', 'Michael', 'Rohan']);

  const [matches, setMatches] = useState([
    { players: ['Eric', 'Sarah'], firstTo: 11, score: [5, 11], winner: 1, serving: 0, states: [] },
    { players: ['Samuel', 'Michael'], firstTo: 11, score: [14, 12], winner: 0, serving: 1, states: [] }
  ]);

  const [matchIndex, setMatchIndex] = useState(null);


  function createPlayer(playerName) {
    if (!playerName) {
      return
    }
    SetPlayers(players.concat([playerName]));
  }

  function createMatch(newMatch) {
    if (!newMatch.players[0] || !newMatch.players[1] || newMatch.players[0] === newMatch.players[1]) {
      console.log('no')
      return
    }
    const prevMatches = JSON.parse(JSON.stringify(matches));
    setMatches(prevMatches.concat([JSON.parse(JSON.stringify(newMatch))]));
  }

  function addPoint(team) {
    const prevMatches = JSON.parse(JSON.stringify(matches));
    const match = prevMatches[matchIndex];
    match.states.push({
      score: match.score.slice(),
      serving: match.serving,
      winner: match.winner
    })
    match.score[team] += 1;
    if (match.score.reduce((a, b) => a+ b) % 2 === 0) {
      match.serving = 1 - match.serving;
    }
    for (let i = 0; i < 2; i ++) {
      if (match.score[i] > match.firstTo & match.score[i] > match.score[1 - i] + 1) {
        match.winner = i;
      }
    }
    setMatches(prevMatches);
  }

  function undo() {
    const prevMatches = JSON.parse(JSON.stringify(matches));
    const match = prevMatches[matchIndex];
    if (! match.states.length) {
      return
    }
    const prevState = match.states.pop();
    match.score = prevState.score;
    match.serving = prevState.serving;
    match.winner = prevState.winner;
    setMatches(prevMatches);
  }

  return (
    <div className="App">

      {matchIndex === null ?
        <div>
          <h2>Matches</h2>
          <CreateMatchForm createMatch={createMatch} players={players} />
          <br />
          {
            matches.slice().reverse().map((match, i) => {
              return <MatchSummary key={`match-summary-${i}`} match={match} onClick={() => { setMatchIndex(matches.length - i - 1) }} />
            })
          }

          <h2>Players</h2>
          <CreatePlayerForm createPlayer={createPlayer} />
          <ul style={{ padding: 0 }}>
            {
              players.slice().reverse().map(player => {
                return <li style={{ listStyleType: "none" }} key={`player-${player}`}>{player}</li>
              })
            }
          </ul>
        </div>
        :
        <div>
          <button className="match" onClick={() => addPoint(0)}>
            <strong>{matches[matchIndex].score[0]}</strong>
            <br />
            {matches[matchIndex].serving === 0 ? "üèì" : ""}  {matches[matchIndex].players[0]}
          </button>
          <div style={{ width: '180px', height: '28px', textAlign: 'left', padding: '6px' }}>
            <button onClick={() => undo()}>undo</button>
          </div>
          <button className="match" onClick={() => addPoint(1)}>
            {matches[matchIndex].serving === 1 ? "üèì" : ""} {matches[matchIndex].players[1]}<br />
            <strong>{matches[matchIndex].score[1]}</strong>
          </button>
          <button onClick={() => setMatchIndex(null)}>üè†</button>

        </div>

      }
    </div>
  );
}

export default App;
